.page__body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__hands {
  position: relative;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: move-hand;
  animation-duration: 3600s;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  top: -20vmin;
  left: 1vmin;
}

.stopwatch__second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: move-hand;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  position: absolute;
  top: -38vmin;
  left: 1.75vmin;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation-duration: 10s;
}

.stopwatch__center {
  z-index: 1;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

@keyframes move-hand {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
